@import '../../sass/global.scss';

.cart{
  font-family: $font-family-primary;
  background-color: $color-tertiary;
  margin-top: 66px;
  display: flex;
  flex-direction: column;
  min-height: 741px;
  max-height: 100%;
  margin-top: 15%;

  .product{
    padding: 2%;
    box-shadow: 0 0 0.4rem 0 rgba(0, 0, 0, .2);
    border-radius: 5px;
    width: 80%;
    margin: 0px auto;
    margin-bottom: 10%;
    background-color: #fff;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
  
    .info{
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
    }
  
    .image{
      margin: 20px;
      width: 200px;
      height: 300px;
    }
  
    .name{
      padding-bottom: 0%;
      font-size: 40px;
    }
  
    .price{
      margin: 10px 0px;
      font-size: 40px;
    }
  
    .quantity{
      margin: 0px;
      font-size: 20px;
    }
  
    .btnRemove{
      text-align: center;
      text-decoration: none;
      letter-spacing: 1px;
      width: 120px;
      padding: 5px 10px;
      font-size: 15px;
      outline: none;
      color: #fff;
      border: 3px solid #fff;
      background-color: #000;
      transition: 0.5s ease;
      display: block;
      margin: 0px;
      font-size: 30px;
    }
  }

  .clearCart{
    text-align: center;
    font-size: 30px;
  }

  .backToShop{
    text-align: center;
    text-decoration: none;
    letter-spacing: 1px;
    display: block;
    width: 200px;
    padding: 5px 10px;
    font-size: 25px;
    outline: none;
    color: #fff;
    border: 3px solid #fff;
    background-color: #000;
    transition: 0.5s ease;
    display: block;
    margin: 0px auto;
  }

  .infoCart{
    margin-top: 5%;
    margin-bottom: 5%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;

    .btnClear{
      text-align: center;
      text-decoration: none;
      letter-spacing: 1px;
      width: 200px;
      padding: 0px 10px;
      font-size: 15px;
      outline: none;
      color: #fff;
      border: 3px solid #fff;
      background-color: #000;
      transition: 0.5s ease;
      display: block;
      margin: 0px;
      font-size: 30px;
    }
    
    .btnClear:hover {
      color: #000;
      background-color: transparent;
      border: 2px solid #000;
    }
  }

  .cleanCart{
    .title{
      text-align: center;
      font-size: 30px;
      margin-bottom: 20px;
    }
    
    .backToShop{
      text-align: center;
      text-decoration: none;
      letter-spacing: 1px;
      display: block;
      width: 200px;
      padding: 5px 10px;
      font-size: 25px;
      outline: none;
      color: #fff;
      border: 3px solid #fff;
      background-color: #000;
      transition: 0.5s ease;
      display: block;
      margin: 0px auto;
    }
  }

  .infoCart{
    margin-top: 5%;
    margin-bottom: 5%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;

    .finalPrice{
      font-weight: 600;
      font-size: 30px;
      margin-right: 3%;
    }
  }
}